import { Field, Input, Picker, Popup, Toast } from "@taroify/core"
import { ArrowRight } from "@taroify/icons"
import { useMemo, useState } from "react"
import Block from "../../../components/block"
import CustomWrapper from "../../../components/custom-wrapper"
import Page from "../../../components/page"
import "./index.scss"

function BasicPicker() {
  const columns = useMemo(() => [
    { label2: "", value: "Hangzhou" },
    { label2: "", value: "Ningbo" },
    { label2: "", value: "Wenzhou" },
    { label2: "", value: "Shaoxing" },
    { label2: "", value: "Huzhou" }
  ], [])

  return (
    <>
      <Toast id="toast" />
      <Picker
        title=""
        columns={columns}
        columnsFieldNames={{ label: "label2" }}
        onChange={(value) => Toast.open(`${value}`)}
        onConfirm={(value) => Toast.open(`${value}`)}
      >
      </Picker>
    </>
  )
}

