function PickerPopup() {
  const [value, setValue] = useState("")
  const [openPicker, setOpenPicker] = useState(false)
  const columns = useMemo(() => [
    { label: "", value: "Hangzhou" },
    { label: "", value: "Ningbo" },
    { label: "", value: "Wenzhou" },
    { label: "", value: "Shaoxing" },
    { label: "", value: "Huzhou" }
  ], [])
  return (
    <>
      <Field label="" rightIcon={<ArrowRight />} onClick={() => setOpenPicker(true)}>
        <Input readonly placeholder="" value={value} />
      </Field>
      <Popup open={openPicker} rounded placement="bottom" onClose={setOpenPicker}>
        <Popup.Backdrop />
        <Picker
          title=""
          cancelText=""
          confirmText=""
          columns={columns}
          onCancel={() => setOpenPicker(false)}
          onConfirm={(values) => {
            setValue(values as string)
            setOpenPicker(false)
          }}
        >
        </Picker>
      </Popup>
    </>
  )
}

function ManualPicker() {
  return (
    <>
      <Toast id="toast" />
      <Picker
        onChange={(value) => Toast.open(`${value}`)}
        onConfirm={(value) => Toast.open(`${value}`)}
      >
        <Picker.Toolbar>
          <Picker.Button></Picker.Button>
          <Picker.Title></Picker.Title>
          <Picker.Button></Picker.Button>
        </Picker.Toolbar>
        <Picker.Column>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
        </Picker.Column>
        <Picker.Column>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
        </Picker.Column>
      </Picker>
    </>
  )
}

